<template>
  <div class="flashcard-quiz p-3">
    <!-- ÈñãÂßãÊåâÈàï -->
    <div
      v-if="!quizStarted"
      class="text-center"
    >
      <h2>ÈñÉÂç°Â≠∏ÁøíÊ∏¨È©ó</h2>
      <button
        class="btn btn-primary mt-3"
        @click="startQuiz"
      >
        ÈñãÂßãÊ∏¨È©ó
      </button>
    </div>

    <!-- Ê∏¨È©ó‰∏≠ -->
    <div v-else-if="quizInProgress">
      <div class="d-flex justify-content-between mb-2 align-items-center">
        <div>È°åÁõÆ {{ currentQuestionIndex + 1 }} / {{ questions.length }}</div>
        <div>Ââ©È§òÊôÇÈñì: {{ timeLeft }}s</div>
      </div>
      <div
        class="progress mb-3"
        style="height: 20px;"
      >
        <div
          class="progress-bar"
          role="progressbar"
          :style="{ width: (timeLeft / totalTimePerQuestion * 100) + '%' }"
          :class="{'bg-danger': timeLeft <= totalTimePerQuestion * 0.3}"
        >
          {{ Math.ceil((timeLeft / totalTimePerQuestion) * 100) }}%
        </div>
      </div>

      <div class="card mb-3">
        <div class="card-body">
          <p class="card-text">
            <strong>{{ questions[currentQuestionIndex].promptLabel }}</strong>
          </p>
          <h4 class="card-title">
            {{ questions[currentQuestionIndex].prompt }}
          </h4>
        </div>
      </div>

      <div class="list-group mb-3">
        <button
          v-for="(choice, idx) in questions[currentQuestionIndex].choices"
          :key="idx"
          type="button"
          class="list-group-item list-group-item-action"
          :class="{
            'list-group-item-success': answered && choice === questions[currentQuestionIndex].correctAnswer,
            'list-group-item-danger': answered && choice === selectedChoice && choice !== questions[currentQuestionIndex].correctAnswer
          }"
          :disabled="answered"
          @click="selectChoice(choice)"
        >
          {{ choice }}
        </button>
      </div>
    </div>

    <!-- Ê∏¨È©óÁµêÊûú -->
    <div
      v-else
      class="text-center"
    >
      <h2>Ê∏¨È©óÂÆåÊàêÔºÅ</h2>
      <p>Á≠îÂ∞ç {{ correctCount }} / {{ questions.length }} È°å</p>
      <button
        class="btn btn-primary"
        @click="restartQuiz"
      >
        ÈáçÊñ∞ÈñãÂßã
      </button>
      <div class="mt-4 text-start">
        <h4>Ë©≥Á¥∞ÁµêÊûúÔºö</h4>
        <ul class="list-group">
          <li
            v-for="(res, idx) in userAnswers"
            :key="idx"
            class="list-group-item"
          >
            È°åÁõÆ {{ idx + 1 }}: 
            <strong>{{ res.promptLabel }}: {{ res.prompt }}</strong><br>
            ‰Ω†ÁöÑÂõûÁ≠î: {{ res.selected }} 
            <span
              v-if="res.correct"
              class="text-success"
            >‚úîÔ∏è Ê≠£Á¢∫</span>
            <span
              v-else
              class="text-danger"
            >‚ùå ÈåØË™§ÔºåÊ≠£Á¢∫Á≠îÊ°à: {{ res.correctAnswer }}</span>
            ÔºåËä±Ë≤ªÊôÇÈñì: {{ res.timeUsed }}s
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import rawJsonData from '@/JSON/V.json'; // Á¢∫Ë™çË∑ØÂæëÊ≠£Á¢∫

export default {
  name: 'FlashcardQuiz',
  data() {
    return {
      jsonData: this.normalizeData(rawJsonData), // Ê≠£Ë¶èÂåñÂæåÁöÑË≥áÊñô
      quizStarted: false,
      quizInProgress: false,
      questions: [],
      currentQuestionIndex: 0,
      totalTimePerQuestion: 30,
      timeLeft: 0,
      timerId: null,
      answered: false,
      selectedChoice: null,
      userAnswers: [],
    };
  },
  computed: {
    correctCount() {
      return this.userAnswers.filter(item => item.correct).length;
    }
  },
  beforeDestroy() {
    this.clearTimer();
  },
  methods: {
    // 1. Ê≠£Ë¶èÂåñË≥áÊñôÔºötrim whitespace„ÄÅÂéªÊéâ newlineÔºåÂ∞çÂèØËÉΩÂ§öÈáç value Âè™‰øùÁïôÁ¨¨‰∏ÄË°åÊàñÂÅö join
    normalizeData(dataArray) {
      return dataArray.map(item => {
        const norm = {};
        // ËôïÁêÜÊØèÂÄãÈóúÊ≥®Ê¨Ñ‰ΩçÔºöÂèñÁ¨¨‰∏ÄË°å‰∏¶ trim
        ['name', 'scientificName', 'english', 'family', 'part', 'pattern', 'chemicalProperties'].forEach(key => {
          let val = item[key];
          if (typeof val === 'string') {
            // ÊãÜË°åÔºåÂè™ÂèñÁ¨¨‰∏ÄË°åÔºåÂÜç trim
            const firstLine = val.split('\n')[0].trim();
            norm[key] = firstLine;
          } else {
            norm[key] = val;
          }
        });
        // Ëã•Ë¶Å‰øùÁïô note„ÄÅdiseaseÔºåÂèØÂú®Ê≠§Âä†ÂÖ•
        norm.note = item.note || '';
        norm.disease = item.disease || '';
        return norm;
      });
    },
    startQuiz() {
      // Èö®Ê©ü shuffle ‰∏îÊåëÈÅ∏Ââç 10ÔºàËã•Â∞ëÊñº 10 Á≠ÜÔºåÂèñÂÖ®ÈÉ®Ôºâ
      const shuffled = this.shuffleArray(this.jsonData.slice());
      const selectedItems = shuffled.slice(0, Math.min(10, shuffled.length));
      // ÁîüÊàêÈ°åÁõÆÈô£Âàó
      this.questions = selectedItems.map(item => this.generateQuestion(item)).filter(q => q !== null);
      // Ëã•Êúâ‰∫õÈ°åÂûãÂõ† distractors ‰∏çË∂≥ËÄåÂõûÂÇ≥ nullÔºåÂèØÂæóÂà∞Â∞ëÊñº 10 È°å
      // Ëã•ÊÉ≥‰∏ÄÂÆöË¶Å 10 È°åÔºåÂèØÂÜçÂÅö‰∏ÄÊ¨°Â°´Ë£úÊàñÈáçÊñ∞ÂëºÂè´ startQuiz()
      if (this.questions.length === 0) {
        alert('È°åÂ∫´ÊàñÂπ≤ÊìæÈÅ∏È†Ö‰∏çË∂≥ÔºåÁÑ°Ê≥ïÁîüÊàêÈ°åÁõÆ');
        return;
      }
      this.quizStarted = true;
      this.quizInProgress = true;
      this.currentQuestionIndex = 0;
      this.userAnswers = [];
      this.startTimer();
    },
    restartQuiz() {
      this.quizStarted = false;
      this.quizInProgress = false;
      this.questions = [];
      this.currentQuestionIndex = 0;
      this.userAnswers = [];
      this.clearTimer();
      this.answered = false;
      this.selectedChoice = null;
    },
    shuffleArray(arr) {
      for (let i = arr.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [arr[i], arr[j]] = [arr[j], arr[i]];
      }
      return arr;
    },
    // 2. ÁîüÊàêÂñÆÈ°åÔºöÂèØËÉΩÂõûÂÇ≥ null Ë°®Á§∫Ê≠§È°åÁÑ°Ê≥ïÁîüÊàêÔºàÂ¶ÇÂπ≤Êìæ‰∏çË∂≥Ôºâ
    generateQuestion(item) {
      // ÂÆöÁæ©È°åÂûãÔºöpromptField, answerField, promptLabel
      const types = [
        { promptField: 'english', answerField: 'name', promptLabel: '‰ª•‰∏ãÂì™‰∏ÄÂÄãÊòØÊ≠§Ëã±ÊñáÂêçÁ®±Â∞çÊáâÁöÑ‰∏≠ÊñáÔºü' },
        { promptField: 'name', answerField: 'english', promptLabel: '‰ª•‰∏ãÂì™‰∏ÄÂÄãÊòØÊ≠§‰∏≠ÊñáÂêçÁ®±Â∞çÊáâÁöÑËã±ÊñáÔºü' },
        { promptField: 'name', answerField: 'family', promptLabel: '‰ª•‰∏ãÂì™‰∏ÄÂÄãÊòØÊ≠§‰∏≠ÊñáÂêçÁ®±Â∞çÊáâÁöÑÁßëÂà•Ôºü' },
        { promptField: 'name', answerField: 'scientificName', promptLabel: '‰ª•‰∏ãÂì™‰∏ÄÂÄãÊòØÊ≠§‰∏≠ÊñáÂêçÁ®±Â∞çÊáâÁöÑÂ≠∏ÂêçÔºü' },
        { promptField: 'name', answerField: 'part', promptLabel: '‰ª•‰∏ãÂì™‰∏ÄÂÄãÊòØÊ≠§‰∏≠ÊñáÂêçÁ®±Â∏∏Áî®ÁöÑÈÉ®‰ΩçÔºü' },
        { promptField: 'name', answerField: 'pattern', promptLabel: '‰ª•‰∏ãÂì™‰∏ÄÂÄãÊòØÊ≠§‰∏≠ÊñáÂêçÁ®±ÁöÑÂ§ñËßÄÂûãÊÖã/ËêÉÂèñÊñπÂºèÔºü' },
        // chemicalProperties Ëã•ÈÅéÊñºÈõÜ‰∏≠ÔºåÂèØ‰ª•‰∏çÂä†ÂÖ•ÔºåÊàñÊîπÊàêÊòØÈùûÈ°åÔºåÈÄôË£°ÂÖà‰∏çËá™ÂãïÂä†ÂÖ•
      ];
      // Èö®Ê©üÈ°åÂûã
      const type = types[Math.floor(Math.random() * types.length)];
      // Ê≠£Á¢∫Á≠îÊ°à
      const correctRaw = item[type.answerField];
      if (!correctRaw) return null; // Ëã•Ê≤íÊúâË©≤Ê¨Ñ‰ΩçÂÄºÔºåÁï•ÈÅé
      const correctAnswer = correctRaw;
      // Êî∂ÈõÜÊâÄÊúâÂèØËÉΩÁöÑ distractor ÂÄº (ÂéªÈô§ËàáÊ≠£Á¢∫Áõ∏Âêå)
      const allOthers = this.jsonData.filter(x => x !== item);
      const distractorSet = new Set();
      for (let other of allOthers) {
        const rawVal = other[type.answerField];
        if (!rawVal) continue;
        const val = rawVal;
        if (val !== correctAnswer) {
          distractorSet.add(val);
        }
      }
      const distractorsArr = Array.from(distractorSet);
      // Â¶ÇÊûú distractorsArr Èï∑Â∫¶ < 3ÔºåÈÅ∏ÊìáÂ∞ëÊñº 3 ÂÄãÂπ≤ÊìæÔºå‰ΩÜËá≥Â∞ëË¶Å 2 (ÂÖ± 3 ÈÅ∏È†Ö) Êàñ 1 (ÂÖ± 2 ÈÅ∏È†Ö)ÔºåË¶ñÈúÄÊ±Ç
      let distractors = [];
      if (distractorsArr.length >= 3) {
        // Èö®Ê©üÊäΩ 3
        const shuffledOthers = this.shuffleArray(distractorsArr.slice());
        distractors = shuffledOthers.slice(0, 3);
      } else if (distractorsArr.length >= 2) {
        const shuffledOthers = this.shuffleArray(distractorsArr.slice());
        distractors = shuffledOthers.slice(0, 2);
      } else {
        // Âπ≤ÊìæÂ§™Â∞ëÔºåË∑≥ÈÅéÊ≠§È°å
        return null;
      }
      // ÁµÑ choices ‰∏¶ shuffle
      const choices = this.shuffleArray([correctAnswer, ...distractors]);
      // prompt
      const promptRaw = item[type.promptField];
      if (!promptRaw) return null;
      const prompt = promptRaw;
      return {
        prompt,
        promptLabel: type.promptLabel,
        choices,
        correctAnswer,
        promptField: type.promptField,
        answerField: type.answerField,
      };
    },
    startTimer() {
      this.clearTimer();
      this.timeLeft = this.totalTimePerQuestion;
      this.answered = false;
      this.selectedChoice = null;
      this.timerId = setInterval(() => {
        if (this.timeLeft > 0) {
          this.timeLeft--;
        } else {
          // ÊôÇÈñìÂà∞ÔºåËá™ÂãïË®òÈåÑÁÇ∫Êú™‰ΩúÁ≠î
          this.recordAnswer(null, true);
        }
      }, 1000);
    },
    clearTimer() {
      if (this.timerId) {
        clearInterval(this.timerId);
        this.timerId = null;
      }
    },
    selectChoice(choice) {
      if (this.answered) return;
      this.clearTimer();
      this.recordAnswer(choice, false);
    },
    recordAnswer(choice, timeUp) {
      this.answered = true;
      this.selectedChoice = choice;
      const q = this.questions[this.currentQuestionIndex];
      const correct = (!timeUp) && (choice === q.correctAnswer);
      const timeUsed = timeUp ? this.totalTimePerQuestion : (this.totalTimePerQuestion - this.timeLeft);
      this.userAnswers.push({
        promptLabel: q.promptLabel,
        prompt: q.prompt,
        selected: choice === null ? 'ÔºàÊú™‰ΩúÁ≠îÔºâ' : choice,
        correct: correct,
        correctAnswer: q.correctAnswer,
        timeUsed: timeUsed,
      });
      // Âª∂ÈÅ≤‰∏ÄÁßíÈÄ≤‰∏ã‰∏ÄÈ°å
      setTimeout(() => {
        this.goToNextQuestion();
      }, 1000);
    },
    goToNextQuestion() {
      this.clearTimer();
      if (this.currentQuestionIndex < this.questions.length - 1) {
        this.currentQuestionIndex++;
        this.startTimer();
      } else {
        this.quizInProgress = false;
      }
    }
  }
};
</script>

<style scoped>
.flashcard-quiz {
  max-width: 600px;
  margin: 0 auto;
}

.list-group-item-success,
.list-group-item-danger {
  position: relative;
  z-index: 2;
  border: none; 
}


.list-group-item {
  margin-bottom: 8px; 
  border-radius: 8px;
  border: 0.5px solid #ccc; 
  transition: all 0.2s ease-in-out;
}


.list-group-item-success {
  box-shadow: 0 0 0 2px #28a745;
  background-color: #d4edda !important;
  font-weight: bold;
}

.list-group-item-danger {
  box-shadow: 0 0 0 2px #dc3545;
  background-color: #f8d7da !important;
  font-weight: bold;
}


.list-group-item:hover:not(:disabled) {
  background-color: #f0f0f0;
  cursor: pointer;
}



</style>
